<template>
    <div class="app">
        <button @click="downloadFile()"></button>
        <button @click="eOpenPage('http://www.baidu.com')"></button>
        <button @click="eLoadPage('http://www.baidu.com')"></button>
        <button @click="selectFile(0)"></button>
        <button @click="selectPPT()">PPT</button>
        <button @click="selectFile(1)"></button>
        <button @click="zipFile()"></button>
        <button @click="openFile()"></button>
        <br />
        selectedFiles:
        <ul>
            <li v-for="item in selectedFiles" :key="item">{{item}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'App',
    data () {
        return {
            selectedFiles: []
        }
    },
    methods: {
        async selectFile (isDirectory) {
            try {
                const targetPath = isDirectory ? await this.eSelectFolders() : await this.eSelectFiles()
                this.selectedFiles = this.selectedFiles.concat(targetPath)
            } catch (error) {
                console.error(error)
            }
        },
        async selectPPT () {
            try {
                const res = await this.eSelectFile({ filters: 'ppt' })
                console.log(res)
            } catch (error) {
                console.error(error)
            }
        },
        async zipFile () {
            try {
                const targetPath = await this.eSelectFolder()
                const zipResult = await this.eZipFile(targetPath, this.selectedFiles)
                console.log(zipResult)
            } catch (error) {
                console.error(error)
            }
        },
        async downloadFile () {
            try {
                const res = await this.eDownload('http://192.168.1.110:8092/feifanScore/nologin/export?desc=true&examRoom=93ba0f3a1e8144a0bff1f531bb2145f3&projectId=456d61a6c80d4ab78bf2d57cc2d52456&type=1')
                console.log('', res)
            } catch (error) {
                console.error(error)
            }
        },
        async openFile () {
            try {
                this.eOpenFile(await this.eSelectFile())
            } catch (error) {
                console.error(error)
            }
        }
    }
}
</script>

<style lang="scss">
.app {}
</style>
